@import './Colors.scss';

.header {
  margin: auto;
  width: 327px;
  padding-left: 24px;
  padding-right: 23px;

  h1 {
    margin: 0;
    padding-top: 80px;
    font-weight: bold;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.14286px;
    color: $Magenta;

    @media screen and (min-width: 768px) {
      font-size: 48px;
      letter-spacing: -1.71px;
      line-height: 48px;
    }
  }

  p {
    margin: 0;
    padding-top: 18px;
    font-weight: 500;
    font-size: 15px;
    line-height: 25px;
    text-align: center;
    letter-spacing: -0.5px;
    font-weight: 500;
    color: $DarkGrayishMagenta;

    @media screen and (min-width: 768px) {
      font-size: 15px;
      letter-spacing: -0.5px;
      line-height: 25px;
    }
  }
}
